.bg-light-grey {
    background: $bg-color-light-grey;

    &:before {
        background: $bg-color-light-grey;
    }
}

.bg-lighter-grey {
    background: $bg-color-lighter-grey;

    &:before {
        background: $bg-color-lighter-grey;
    }
}

.bg-pink-grey {
    background: $bg-color-pink-grey;

    &:before {
        background: $bg-color-pink-grey;
    }
}

main {
    overflow: hidden;
}

.cover {
    width: 100%;
    min-height: calc(100vh - 169px);
    margin-bottom: 60px;
    position: relative;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    &:not(.b-landing-top) {
        &:before {
            content: '';
            background: $bg-color-lighter-grey;
            opacity: .6;
            position: absolute;
            top: 0;
            right: 0;
            bottom: -3px;
            left: 0;
            z-index: 2;
        }
    }

    .title-wrap {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 3;
        transform: translate(-50%, -50%);
    }
    
    &.no-margin-bottom {
        margin-bottom: 0;
    }

}

#error-page {
    .title-wrap {
        max-width: 600px;
        top: 40%;
    }
}

.middle, .wp-block-group {
    width: 100%;
    max-width: 900px;
    margin: 0 auto;
    padding-left: $mobile-padding;
    padding-right: $mobile-padding;

    h2 {
        text-align: left;
    }

    &.ptop {
        padding-top: 60px;
    }
}

.full-bg {
    position: relative;

    * {
        z-index: 3;
        position: relative;
    }

    &:before {
        content: '';
        display: block;
        position: absolute;
        top: 20px;
        right: -100%;
        bottom: 30px;
        left: -100%;
        z-index: 1;
    }
}

.hidden {
    display: none;
}

.clear {
	display: block;
	clear: both;
}

.clearfix:after {
	content: "";
	display: table;
	clear: both;
}

@include mq-800 {
    .cover {
        min-height: calc(100vh - 61px);

        .title-wrap {
            top: 40%;
            text-align: center;
            width: 90%;
        }
    }
}